<h1 id="撤销订单" tabindex="-1"><a class="header-anchor" href="#撤销订单" aria-hidden="true">#</a> 撤销订单</h1>
<p>目前只有 <strong>刷卡支付</strong> 有此功能。</p>
<blockquote>
<p>调用支付接口后请勿立即调用撤销订单API，建议支付后至少15s后再调用撤销订单接口。</p>
</blockquote>
<h2 id="通过内部订单号撤销订单" tabindex="-1"><a class="header-anchor" href="#通过内部订单号撤销订单" aria-hidden="true">#</a> 通过内部订单号撤销订单</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">reverse</span><span class="token operator">-></span><span class="token function">byOutTradeNumber</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"商户系统内部的订单号（out_trade_no）"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><h2 id="通过微信订单号撤销订单" tabindex="-1"><a class="header-anchor" href="#通过微信订单号撤销订单" aria-hidden="true">#</a> 通过微信订单号撤销订单</h2>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">reverse</span><span class="token operator">-></span><span class="token function">byTransactionId</span><span class="token punctuation">(</span><span class="token string double-quoted-string">"微信的订单号（transaction_id）"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div>